body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

body {
  background: linear-gradient(
    45deg,
    #ec008c,
    #fc6767,
    #fdbc3d,
    #2ad2c9,
    #9fe0f6,
    #9fe0f6
  );
  background-size: 600% 600%;
  -webkit-animation: gradient 15s ease infinite;
          animation: gradient 15s ease infinite;
}

@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto 1fr;
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  max-width: 900px;
  margin: 0 auto;
  padding: 30px;
  align-items: center;
}

.welcome {
  grid-column: 1 / span 3;
  grid-row: 1;
  padding: 10px;
  display: flex;
  justify-content: center;
  text-align: center;
  color: black;
  background-color: rgba(127, 255, 212, 0.7);
  border: 3px solid aqua;
  border-radius: 20px;
}
.smallIntro {
  grid-column: 1 / span 3;
  grid-row: 2;
  padding: 10px;
  display: flex;
  justify-content: center;
  text-align: center;
  color: black;
  background-color: rgba(127, 255, 212, 0.7);
  border: 3px solid aqua;
  border-radius: 20px;
}

.intro {
  background-color: rgba(127, 255, 212, 0.7);
  padding: 10px;
  display: flex;
  color: black;
  border: 3px solid aqua;
  border-radius: 20px;
  align-items: center;
}
.learnMore {
  background-color: rgba(127, 255, 212, 0.3);
  padding: 10px;
  display: flex;
  color: black;
  border: 3px solid aqua;
  border-radius: 20px;
  align-items: center;
}

.icon-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  align-items: center;
  width: 100px;
  height: 100px;
  color: aqua;
}

.icon-container span {
  font-size: 0.8em;
  margin-top: 5px;
}

.icon-container:hover {
  color: blue;
  height: 125px;
  width: 125px;
}

.icon-java {
  grid-column: 2;
  grid-row: 2;
  width: 100px;
  height: 100px;
}

.icon-apollo {
  grid-column: 2;
  grid-row: 2;
  width: 100px;
  height: 100px;
}

.icon-query {
  grid-column: 2;
  grid-row: 2;
  width: 100px;
  height: 100px;
}

.icon-react {
  grid-column: 1;
  grid-row: 2;
  width: 100px;
  height: 100px;
}

.icon-node {
  grid-column: 3;
  grid-row: 2;
  width: 100px;
  height: 100px;
}

.icon-sql {
  grid-column: 1;
  grid-row: 2;
  width: 100px;
  height: 100px;
}
.icon-api {
  grid-column: 1;
  grid-row: 2;
  width: 100px;
  height: 100px;
}

.icon-html {
  grid-column: 3;
  grid-row: 1;
  width: 100px;
  height: 100px;
}

.icon-css {
  grid-column: 2;
  grid-row: 1;
  width: 100px;
  height: 100px;
}

@media (max-width: 768px) {
  .grid-container {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }

  .icon-java,
  .icon-html,
  .icon-css,
  .icon-sql,
  .icon-node,
  .icon-react,
  .icon-apollo,
  .icon-query,
  .icon-api {
    justify-content: center;
    margin: 20px 0;
  }
}

.infoButton {
  background-color: rgba(127, 255, 212, 0.7);
  padding: 10px;
  display: flex;
  color: black;
  border: 3px solid aqua;
  border-radius: 20px;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

.secHeader {
  text-align: center;
  border-radius: 20px;
}
.aboutMeContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.selfieIMG {
  width: 300px;
  height: 300px;
  margin: 10px auto;
  border: 4px solid black;
  border-radius: 20px;
  overflow: hidden;
}
.selfieIMG img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.aboutMe {
  background-color: aquamarine;
  color: black;
  border: 3px solid black;
  padding: 20px;
  border-radius: 20px;
  text-align: center;
  margin: 10px auto;
  max-width: 80%;
}

/* Media Query for responsive design */
@media screen and (max-width: 768px) {
  .aboutMeContainer {
    flex-direction: column;
  }
  .selfieIMG {
    margin: 0 0 20px 0;
  }
}

p {
  font-size: 25px;
  line-height: 50px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.carousel {
  display: flex;
  justify-content: center;
}

.carousel .slide {
  height: 80vh;
  width: 70vh;
  position: relative;
}

.carousel .slide img {
  max-height: 100%;
  max-width: 80%;
  width: auto;
}

.project-description {
  position: absolute;
  bottom: -6%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  padding: 10px;
  margin: 0;
  width: 80%;
  height: 35%;
  text-align: center;
}

@media only screen and (max-width: 1200px) {
  .carousel {
    height: 90vh;
  }

  .carousel .slide img {
    margin: 10vh auto;
    width: 100vw;
  }
}
@media only screen and (max-width: 800px) {
  .carousel {
    height: 80vh;
  }

  .carousel .slide img {
    margin: 10vh auto;
    width: 100vw;
  }
}
@media only screen and (max-width: 600px) {
  .carousel {
    height: 70vh;
  }

  .carousel .slide img {
    margin: 10vh auto;
    width: 100vw;
  }
}
.secHeader {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  max-width: 50%;
  background-color: aquamarine;
  color: black;
}

.babyJournal {
  display: flex;
  justify-content: center;
  color: aqua;
  margin-top: 1000px;
}

.youtubeVideo .project-description {
  margin-bottom: 0; /* to remove the margin at the bottom */
}

form {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  font-size: 20px;
}

input {
  width: 250px;
  border: 3px solid black;
  border-radius: 7px;
  padding: 5px;
}

textarea {
  width: 300px;
  height: 150px;
  border: 3px solid black;
  border-radius: 7px;
  padding: 5px;
}

input[type="submit"] {
  margin-top: 10px;
  margin-bottom: 10px;
  cursor: pointer;
  background-color: black;
  color: aqua;
}

label {
  display: flex;
  color: black;
  background-color: aquamarine;
  border: 3px solid black;
  border-radius: 7px;
  padding: 5px;
  margin: 5px auto;
}

.resumeContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.resumeIMG {
  justify-content: center;
  border: 3px solid black;
  margin-top: 10px;
}

.iconContainer {
  display: flex;
  justify-content: center;
}

.icon-youtube {
  color: red;
  height: 50px;
  width: 50px;
}
.icon-linkedin {
  color: rgb(91, 177, 226);
  height: 50px;
  width: 50px;
}
.icon-github {
  color: black;
  height: 50px;
  width: 50px;
}

.icon-youtube:hover {
  color: rgb(129, 34, 34);
  height: 65px;
  width: 65px;
}
.icon-linkedin:hover {
  color: rgb(15, 92, 170);
  height: 65px;
  width: 65px;
}
.icon-github:hover {
  color: rgb(190, 185, 185);
  height: 65px;
  width: 65px;
}

